.city-preview .title.wrapper {
  position: relative; }
  .city-preview .title.wrapper .text {
    position: absolute;
    z-index: 3;
    width: 100%;
    top: 0;
    left: 0;
    font-weight: bold; }
  .city-preview .title.wrapper .text.next {
    display: none; }

@-webkit-keyframes text-up-out {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  to {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); } }

@keyframes text-up-out {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  to {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); } }

@-webkit-keyframes text-up-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px); }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes text-up-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px); }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@-webkit-keyframes text-down-out {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  to {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px); } }

@keyframes text-down-out {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  to {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px); } }

@-webkit-keyframes text-down-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes text-down-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }
  .city-preview .title.wrapper .text.scroll-up.leaving {
    -webkit-animation: text-up-out 250ms both;
            animation: text-up-out 250ms both; }
  .city-preview .title.wrapper .text.scroll-up.entering {
    display: initial;
    -webkit-animation: text-up-in 250ms both;
            animation: text-up-in 250ms both; }
  .city-preview .title.wrapper .text.scroll-down.leaving {
    -webkit-animation: text-down-out 250ms both;
            animation: text-down-out 250ms both; }
  .city-preview .title.wrapper .text.scroll-down.entering {
    display: initial;
    -webkit-animation: text-down-in 250ms both;
            animation: text-down-in 250ms both; }



